import imgur from "imgur"

export const getAlbum = (payload) => {
    return dispatch => {
        var kittenAlbum = payload.url;
        imgur.getAlbumInfo(kittenAlbum)
            .then((json)=>{
                dispatch({type:"ALBUMGAINED", album:json, class:payload.class})
            })
            .catch((err)=>{
                dispatch({type:"ALBUMERROR", error:err.message})
            });
    };
  };