import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import React from "react";
const Text1 = (props) =>{
    return(
        <Container>
            <h6>The W.H.O.R.E-program is special since it had its own school, and you are standing in the hall with the other freshmen. Ready for the introduction, and looking your fellow students over. A stern-looking man who you can only presume is the headmaster slowly walks into the hall. 
            <br/>"Good morning girls. I hope you are ready for a great year... I will make sure that you follow all the rules of this school to the dot... As a short introduction, I will first read the school's code of conduct so you all know what is expected of you." 
            <br/>Looking out over the crowd of eager women he spots your voluptuous stature and can't help but smile 
            <br/>"First of all breaking any of these rules will lead to punishment. I will be in charge of punishment and rewards for the students that do behave." he announces. Then he goes to find an old parchment from his suit's inner pocket and start to read aloud.
            <br/>"Code of Conduct for the W.H.O.R.E.-program.
            </h6>
            <Button onClick={()=>props.it()} block>Continue</Button>
        </Container>
    )
}

export default Text1;