const initState = {
    album:[],
    error:null,
    class: null,
}

export const firerockReducer = (state = initState, action) =>{
    switch(action.type){
        case "ALBUMGAINED":
            console.log(state)
            console.log(action)
            state = {...state, album: action.album.data.images, class:action.class}
            return state
        case "ALBUMERROR":
            state = {...state, error:action.error}
            return state
        default:
            return state
    }
}