import Container from "react-bootstrap/Container";
import Image from "react-bootstrap/Image";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import React from "react";
const getSpeech = (characterSpeaking, wordsSaid, mood) =>{
    let speechClasses = "";
    let containerClasses="";
    let image = "";
    if(characterSpeaking === "Longwood"){
        if(mood === "none"){
            image = require("./../resources/trainer/teacher/teach.jpeg")
            containerClasses="bg-secondary"
        }
    }

    return <div className={"rounded my-3 py-3 Container "+containerClasses}>
        <Row >
            <Col xs={3} className="mx-3">
                <Image fluid roundedCircle src={image} alt="Generated from function"/>
            </Col>
            <Col xs={8} className="my-auto">
                <h6 className={"text-center "+speechClasses}>{wordsSaid}</h6>
            </Col>
        </Row>
    </div>
}

export default getSpeech;