import React from "react";
import "./imageCSS.css";
import Container from "react-bootstrap/Container";
import Image from "react-bootstrap/Image";

class GetImage extends React.Component{
    constructor(props){
      super(props)
      this.state={
        character: [],
        outfit: [],
        counter: 0
      }
  }
  componentDidMount(){
    this.setState({character:this.props.character, outfit:this.props.character.outfit})
    
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    let body = Object.keys(nextProps.character).map((item)=>{
        if(item !== "outfit" && nextProps.character[item] !== null){
           return <Image key={item + ": "+nextProps.character[item]} fluid src={require("../resources/trainer/hermione/body/"+item+"/"+nextProps.character[item]+".png")} className="image"/>
        }else{
          return null
        }
    })
    const outfit= nextProps.character.outfit;
    let clothes = Object.keys(outfit).map((item)=>{
        if(outfit[item] !== null && outfit[item] !== false){
            return <Image key={item + ": "+outfit[item]} fluid src={require("../resources/trainer/hermione/clothes/"+item+"/"+outfit[item]+".png")} className="image"/>
         }else{
          return null
        }
    })
    return {
     character: body, outfit:clothes
    };
   }

  render(){

    return(
        <Container>
            <div className="block fluid"></div>
            {this.state.character}
            {this.state.outfit}
        </Container>)
  }
}
export default GetImage;