import React from "react";
import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import Text1 from "./text/1";
import Text2 from "./text/Intro2";
import Text3 from "./text/intro3";
import Text4a from "./text/intro4TakeDeal";
import Text4b from "./text/intro4NoDeal";
import TEXT_3_5 from "./text/introFirstChoice";
import Text5 from "./text/intro5";
import TextPathSplit from "./text/pathSplit1";
import CL1 from "./text/controlLine/1";
import CL2 from "./text/controlLine/2";
import CL3 from "./text/controlLine/3";
import Out from "./text/controlLine/out";
import CL4 from "./text/controlLine/4";
import CL35 from "./text/controlLine/3_5";
import CL5 from "./text/controlLine/5";
import CL6 from "./text/controlLine/6";
import TF1 from "./text/tfLine/1";
import Futa1 from "./text/tfLine/futa/1";
import Futa2 from "./text/tfLine/futa/2";
import Mass from "./text/tfLine/mass";
import Class1 from "./text/tfLine/class1";
import Class2 from "./text/tfLine/class2";
import Class3 from "./text/tfLine/class3";
import HairFuta from "./text/tfLine/futa/Hair";
import SexFuta from "./text/tfLine/futa/Sex"
import SexFuta2 from "./text/tfLine/futa/Sex2";

class GetText extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            controlLine: false,
            transformationLine: false,
            instanceTime: 0,
            bool: false,
            sex: false,
        };
    }

    resetTempState = () =>{
        this.setState({controlLine: false, transformationLine: false, bool: false, sex:false})
    }

    render(){
    const {time, action, state} = this.props
    const {character} = state
    switch(time){
        case 0:
            return( 
                <Container >
                    <h2 className="text-center">Welcome to the W.H.O.R.E.-project</h2>
                    <h6>Hi there.
                        ​<br/>
                        This is the W.H.O.R.E.-Project, it is an adult game and should be treated as such. By continuing you acknowledge that you are an appropriate age in the area that you live.
                        ​<br/>
                        Please excuse the bad English, as I am not a native speaker and this is made in an IDE with no spellchecker. 
                        ​<br/>
                        This game is heavily inspired by the game "S.L.U.T.-Academy" by JoeForest. And by inspired I mean I got bored of waiting for updates and decided to make my version. It is ironic that this very game kinda died too.
                        ​<br/>
                        Throughout the game, you will notice pictures of an adult nature. If you like the art check out the original artist "Akabur" since he has made some pretty neat games. The art will be removed upon request and simply uses an image library.
                        ​<br/>
                        Thank you to the following people for writing contributions:<br/>
                        Malmuira + Erinhue + Nbyseu 
​                            </h6>
                        <br/>
                        <Button onClick={this.props.moveForward} block>Play the game</Button>
                        <br/>
                </Container>)
            case 1:
                return(
                    <Text1 it={()=>this.props.moveForward()}/>
                )
            case 2:
                return(
                    <Text2 it={()=>this.props.moveForward()}/>
                )
            case 3:
                
                return(
                    <Text3 action={(item)=>action({character:item})} setPoint={(number)=>{this.props.setPoint(number)}} it={()=>this.props.moveForward()}/>
                )
            case 4:
                return(
                    <TEXT_3_5 setPoint={(number)=>{this.props.setPoint(number)}} it={()=>this.props.moveForward()} action={(object)=>{action(object)}}/>
                )
            case 5:
                
                if(state.bool){
                    return  <Text4a action={(item)=>action({...item})} it={()=>this.props.moveForward()}/>
                }else{
                    return  <Text4b action={(item)=>action({...item})} it={()=>this.props.moveForward()}/>
                }
            case 6:
                return <Text5 character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.props.moveForward()} />
            case 7:
                return <TextPathSplit goControl={()=>this.setState({controlLine: true})} goTransformation={()=>this.setState({transformationLine: true})} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.props.moveForward()}  />

            case 8:
                if(this.state.transformationLine){
                    switch(this.state.instanceTime){
                        case -1:
                            return null;
                        case 0:
                            return(<TF1 setLate={()=>{this.setState({late:true})}} setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} goTransformation={()=>this.setState({transformationLine: true})} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.setState({instanceTime:this.state.instanceTime+1})} />)
                        case 1:
                            if(this.props.state.late){
                            return(<Futa1 setBool={()=>this.setState({bool:true})} setLate={()=>{this.setState({late:true})}} setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} goTransformation={()=>this.setState({transformationLine: true})} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.setState({instanceTime:this.state.instanceTime+1})} />)
                            }else{
                                return <Class1 setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} goTransformation={()=>this.setState({transformationLine: true})} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.setState({instanceTime:this.state.instanceTime+1})}/>
                            }
                        case 2: 
                            if(this.state.bool){
                                return <Futa2 setBool={()=>this.setState({bool:true})} setLate={()=>{this.setState({late:true})}} setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} goTransformation={()=>this.setState({transformationLine: true})} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.setState({instanceTime:this.state.instanceTime+1})}/>
                            }else if(this.props.state.late){
                                return <Mass setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} goTransformation={()=>this.setState({transformationLine: true})} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.setState({instanceTime:this.state.instanceTime+1})} />
                            }else{
                                return <Class2 setBool={this.setState({bool:true})} setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} goTransformation={()=>this.setState({transformationLine: true})} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.setState({instanceTime:this.state.instanceTime+1})} />
                            }
                        case 3:
                            if(character.hair === "lola_blonde" && !this.state.sex){
                                return <HairFuta askSex={()=>{this.setState({sex:true})}} setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} goTransformation={()=>this.setState({transformationLine: true})} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.setState({instanceTime:this.state.instanceTime+1})} />
                            }else if(this.state.bool){
                                return <SexFuta setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} goTransformation={()=>this.setState({transformationLine: true})} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.setState({instanceTime:this.state.instanceTime+1})} toggleSpell={(item)=>this.props.toggleSpell(item)}/>
                            }else{
                                return <Class3 bool={this.state.bool}  late={this.props.state.late} setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} goTransformation={()=>this.setState({transformationLine: true})} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.setState({instanceTime:this.state.instanceTime+1})} />
                            }
                        case 4:
                            if(this.state.bool){
                                return <SexFuta2 setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} goTransformation={()=>this.setState({transformationLine: true})} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.setState({instanceTime:this.state.instanceTime+1})} toggleSpell={(item)=>this.props.toggleSpell(item)}/>
                            }else{

                            }
                        default:
                            this.props.moveForward()
                        }
                }else{
                    switch(this.state.instanceTime){
                        case -1:
                            return(<Out setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} goTransformation={()=>this.setState({transformationLine: true})} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.props.moveForward()} />)
                        case 0:
                            return(<CL1 setIT={(n)=>{this.setState({instanceTime:n})}} setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} goTransformation={()=>this.setState({transformationLine: true})} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.setState({instanceTime:this.state.instanceTime+1})} />)
                        case 1:
                            return(<CL2 setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.setState({instanceTime:this.state.instanceTime+1})} />)
                        case 2:
                            return(<CL3 setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.setState({instanceTime:this.state.instanceTime+1})} />)
                        case 3:
                            return(<CL35 CL3 setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.setState({instanceTime:this.state.instanceTime+1})} />)
                        case 4:
                            return(<CL4 setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.setState({instanceTime:this.state.instanceTime+1})} />)
                        case 5:
                            return(<CL5 setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.setState({instanceTime:this.state.instanceTime+1})}/>)
                        case 6: return(<CL6 setPoint={(number)=>{this.props.setPoint(number)}} getOut={()=>{this.setState({instanceTime:-1})}} goTransformation={()=>this.setState({transformationLine: true})} character={this.props.state.character} points={this.props.points} action={(item)=>action({...item})} it={()=>this.props.moveForward()}/>)
                        default:
                            this.props.moveForward()
                        }
                        
                }
            break;
        default:
            return <div> 
                <p className="text-light">That is all for now folks. Again this is a learning experience. If you have something you wanna share then feel free to leave a comment wherever you found this game.</p>
            </div>
        }   
    }
}

export default GetText;