import React, {Component} from "react"
import Carousel from "react-bootstrap/Carousel"
import Button from "react-bootstrap/Button"
import { connect } from 'react-redux';
import Card from "react-bootstrap/Card"
import Accordion from "react-bootstrap/Accordion"
import CardColumns from "react-bootstrap/CardColumns"
import CardDeck from "react-bootstrap/CardDeck"
import InputGroup from 'react-bootstrap/InputGroup'
import FormControl from "react-bootstrap/FormControl"
import Badge from 'react-bootstrap/Badge'

class Gamemode extends Component {
    constructor(props) {
        super(props);
        this.state = {
            turn:1, 
            manacrystals:1,
            unusedManacrystals: 1, 
            cards:[...props.images], 
            play:false, 
            handsize:-1, 
            hand:[], 
            heropowers:[], 
            sorted: false, 
            board:[],
            spellstack: [],
            tokens: [],
            quests: [] 
        }
    }

    sortDeck = ()=>{
        let deck = [...this.state.cards]
        let newdeck = []
        let heropowers = [...this.state.heropowers]
        let tokens = [...this.state.tokens]
        for(let i = 0 ; i<deck.length ; i++){
            if(deck[i].description !== undefined && deck[i].description !== null){
                let desc = deck[i].description
                let splitdesc = desc.split(" ")
                let tags = []
                for(let k = 0 ; k<splitdesc.length ; k++){
                    if(splitdesc[k].includes("#")){
                        tags = [...tags, splitdesc[k]]
                        splitdesc[k] = ""
                    }
                }
                desc = ""
                for(let k = 0 ; k<splitdesc.length ; k++){
                    desc = desc + splitdesc[k] + " "
                }
                desc = {...JSON.parse(desc), tags:tags}

                if(deck[i].description.includes("#heropower")){
                    heropowers = [...heropowers, {...deck[i], stats: desc}]
                }else if (deck[i].description.includes("#token")){
                    tokens = [...tokens, {...deck[i], stats: desc}]
                }else{
                    newdeck = [...newdeck, {...deck[i], stats: desc}]
                }
            }else{
                console.error("Description not present")
            }
        }
        return {cards:newdeck,tokens:tokens, heropowers:heropowers, sorted:true}
    }

    playcard = (id)=>{
        let hand = [...this.state.hand]
        let board = [...this.state.board]
        let spellstack = [...this.state.spellstack]
        let quests = [...this.state.quests]
        for (let i = 0 ; i < this.state.hand.length ; i++){
            if(hand[i].id === id){
                const card = hand[i]
                if((board.length < 7 || (card.stats.tags !== undefined && (card.stats.tags.includes("#spell") || card.stats.tags.includes("#weapon")))) && Number(card.stats.m) <= this.state.unusedManacrystals){
                    const card = hand[i]
                    hand.splice(i, 1)
                    if(card.stats.tags.includes("#spell") && (card.stats.tags.includes("#quest") || card.stats.tags.includes("#sidequest"))){
                        this.setState({hand:hand, quests:[...quests, card]})
                        break;
                    }else if(card.stats.tags.includes("#spell")){
                        this.setState({hand:hand, spellstack:[...spellstack, card]})
                        break;
                    }else{
                        this.setState({hand:hand, board:[...board, card]})
                        break;
                    }
                }else{
                    console.log("Card can NOT be played")
                    //TODO: Make a warning about being unable to play the cards 
                }
            }
        }
    }

    changeNoteOfCardOnBoard = (e) =>{
        e.preventDefault();
        let board = [...this.state.board]
        for(let i = 0 ; i < board.length; i++){
            if(e.target.name === board[i].id){
                board[i] = {...board[i], stats:{...board[i].stats, note:e.target.value}}
                break;
            }
        }
        this.setState({board:board})
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if(nextProps.images.length > 0){
            let deck = [...prevState.cards]
            let newdeck = []
            let heropowers = [...prevState.heropowers]
            let tokens = [...prevState.tokens]
            for(let i = 0 ; i<deck.length ; i++){
                if(deck[i].description !== undefined && deck[i].description !== null){
                    let desc = deck[i].description
                    let splitdesc = desc.split(" ")
                    let tags = []
                    for(let k = 0 ; k<splitdesc.length ; k++){
                        if(splitdesc[k].includes("#")){
                            tags = [...tags, splitdesc[k]]
                            splitdesc[k] = ""
                        }
                    }
                    desc = ""
                    for(let k = 0 ; k<splitdesc.length ; k++){
                        desc = desc + splitdesc[k] + " "
                    }
                    try {
                        desc = {...JSON.parse(desc), tags:tags, notes:""}
                    } catch(e) {
                        console.warn("Error at: "+ "https://i.imgur.com/" + deck[i].id +"."+ deck[i].type.split("/")[1])
                        console.error(e);
                    }

                    if(deck[i].description.includes("#heropower")){
                        heropowers = [...heropowers, {...deck[i], stats: desc}]
                    }else if (deck[i].description.includes("#token")){
                        tokens = [...tokens, {...deck[i], stats: desc}]
                    }else{
                        newdeck = [...newdeck, {...deck[i], stats: desc}]
                    }
                }else{
                    console.error("Description not present")
                }
            }
            return {cards:newdeck,tokens:tokens, heropowers:heropowers, sorted:true}
        }
    }

    drawCards = (number) =>{

        let deck = [...this.state.cards]
        const min = 1;
        const max = deck.length;
        if(min>max){
            return false
        }
        const rand = Math.floor(Math.random() * (max - min));
        const card = deck[rand]
        deck.splice(rand,1)
        this.setState({
            cards: deck,
            hand: [...this.state.hand, card],
            handsize: this.state.hand.length
        },()=>{
            if(number-1 > 0){
                this.drawCards(number-1)
            }
        })
    }


    render() {
        if(this.state.handsize === -1){ 
            if(!this.state.sorted && this.state.cards.length > 0){
                this.sortDeck()
            }
            const images = this.state.cards.map((item)=>{
                return (
                <Carousel.Item key={"carousel item: "+item.id}>
                    <img key={item.id} src={"https://i.imgur.com/" + item.id +"."+ item.type.split("/")[1]} alt="Card from deck" className="d-block w-100" />
                </Carousel.Item>
                )
                
            })
            return(
                <div>
                    <div className="row">
                        <div className="col-md-5">
                            <Carousel>
                                {images}    
                            </Carousel>
                        </div>
                        <div className="col-md-7 rounded">
                            <h3>You have chosen the {this.props.type} deck.</h3>
                            <p>{this.props.description}</p>
                            <h4>Are you sure you wanna play with this deck?</h4>
                            <Button block size="lg" onClick={()=>{
                                this.drawCards(5)
                            }} variant="success">Yes</Button>
                            <br/>
                            <Button block size="lg" variant="danger">No</Button>
                        </div>
                    </div>
                </div>
            )
            
        }else{
            const handimages = this.state.hand.map((item)=>{
                return (
                    <Card key={item.id} className="">
                        <Card.Img variant="top" src={"https://i.imgur.com/" + item.id +"."+ item.type.split("/")[1]} />
                        <Card.Body>
                            <Button variant="primary" onClick={()=>{this.playcard(item.id)}}>Play</Button>
                        </Card.Body>
                    </Card>
                )
            })

            const board = this.state.board.map((item)=>{
                const tags = item.stats.tags.map((tag)=>{
                    let x = tag.substr(1)
                    x = x.charAt(0).toUpperCase() + x.slice(1);
                return <Badge variant="secondary">{x}</Badge>
                })
                return (
                    <Card key={item.id} className="">
                        <div className="row">
                            <div className="col-6">
                                <Card.Img variant="top" src={"https://i.imgur.com/" + item.id +"."+ item.type.split("/")[1]} />
                            </div>
                            <div className="col-6">
                            <InputGroup key={"inputgroup"+item.id}>
                                <InputGroup.Prepend>
                                <InputGroup.Text>Notes for this card</InputGroup.Text>
                                </InputGroup.Prepend>
                                <FormControl name={item.id} key={"Formcontrol"+item.id} as="textarea" aria-label={"Textare for "+item.id} defaultValue={item.stats.note} onChange={this.changeNoteOfCardOnBoard} />
                            </InputGroup>
                            <br/>
                            <h3>Tags</h3>
                            <h2 key={"tags"+item.id}>
                                <Badge key={"badgeTags: "+item.id} variant="secondary">{tags}</Badge>
                            </h2>
                            </div>
                        </div>
                    </Card>
                )
            })
           

            return ( <div>
                <div className="row">
                    <div className="col-md-9">
                        <Accordion>
                            <Card>
                                <Card.Header>
                                <Accordion.Toggle as={Button} variant="dark" eventKey="0">
                                    Show me my hand
                                </Accordion.Toggle>
                                </Card.Header>
                                <Accordion.Collapse eventKey="0">
                                <Card.Body>
                                    <CardColumns>
                                        {handimages}
                                    </CardColumns>
                                </Card.Body>
                                </Accordion.Collapse>
                            </Card>
                            <Card>
                                <Card.Header>
                                <Accordion.Toggle as={Button} variant="dark" eventKey="1">
                                    Show me the board
                                </Accordion.Toggle>
                                </Card.Header>
                                <Accordion.Collapse eventKey="1">
                                <Card.Body>
                                    <h3>Creatures on the board</h3>
                                    {board}
                                    <hr/>
                                    <h3>Spells that you cast </h3>
                                </Card.Body>
                                </Accordion.Collapse>
                            </Card>
                            <Card>
                                <Card.Header>
                                <Accordion.Toggle as={Button} variant="dark" eventKey="2">
                                    Set current heropower and play tokens
                                </Accordion.Toggle>
                                </Card.Header>
                                <Accordion.Collapse eventKey="2">
                                    <Card.Body>
                                        <CardDeck>
                                            <Card>
                                                <Card.Img variant="top" src="holder.js/100px160" />
                                                <Card.Body>
                                                <Card.Title>Card title</Card.Title>
                                                <Card.Text>
                                                    This is a wider card with supporting text below as a natural lead-in to
                                                    additional content. This content is a little bit longer.
                                                </Card.Text>
                                                </Card.Body>
                                                <Card.Footer>
                                                <small className="text-muted">Last updated 3 mins ago</small>
                                                </Card.Footer>
                                            </Card>
                                            <Card>
                                                <Card.Img variant="top" src="holder.js/100px160" />
                                                <Card.Body>
                                                <Card.Title>Card title</Card.Title>
                                                <Card.Text>
                                                    This card has supporting text below as a natural lead-in to additional
                                                    content.{' '}
                                                </Card.Text>
                                                </Card.Body>
                                                <Card.Footer>
                                                <small className="text-muted">Last updated 3 mins ago</small>
                                                </Card.Footer>
                                            </Card>
                                            <Card>
                                                <Card.Img variant="top" src="holder.js/100px160" />
                                                <Card.Body>
                                                <Card.Title>Card title</Card.Title>
                                                <Card.Text>
                                                    This is a wider card with supporting text below as a natural lead-in to
                                                    additional content. This card has even longer content than the first to
                                                    show that equal height action.
                                                </Card.Text>
                                                </Card.Body>
                                                <Card.Footer>
                                                <small className="text-muted">Last updated 3 mins ago</small>
                                                </Card.Footer>
                                            </Card>
                                        </CardDeck>
                                    </Card.Body>
                                </Accordion.Collapse>
                            </Card>
                        </Accordion>
                    </div>
                    <div className="col-md-3">        
                        <Button variant="info" block onClick={()=>{this.drawCards(1)}}>Draw a card</Button>
                        <Button variant="warning" block>End turn</Button>
                        <br/>
                        <Button variant="success" block>Get paste of your <strong>gameboard</strong></Button>
                        <Button variant="success" block>Get paste of your <strong>turn</strong></Button>
                    </div>
                </div>              
            </div> );
        } 
    }
}

const MapStateToProps = (state) =>{
    console.log(state)
    return {
        images: state.firerockReducer.album,
        deckclass: state.firerockReducer.class
    }
}
 
export default connect(
    MapStateToProps,
    null
  )(Gamemode);