import React from 'react';
import './App.css';
import {connect} from "react-redux";
import Navbar from 'react-bootstrap/Navbar';
import Nav from "react-bootstrap/Nav";

class App extends React.Component{
  constructor(props){
    super(props)
    this.state={
        num: null
    }
}
  reset = () =>{
    this.props.reset()
    this.props.resetItems()
  }

  render(){
    const {pathname} = window.location
    return(
      <Navbar className="navbar navbar-dark bg-dark sidebar">
        <Navbar.Brand><button className="btn btn-light" onClick={()=>this.reset()}>Reset</button></Navbar.Brand>
        <Nav className="mr-auto">
          {pathname !== "/" ? <Nav.Link href="/" onSelect={()=>this.reset()}>Landing</Nav.Link> :null}
          {pathname !== "/MightyPen" ? <Nav.Link href="/MightyPen" onSelect={()=>this.reset()}>Mighty Pen</Nav.Link>: null}
          {pathname !== "/AIHousing" ? <Nav.Link href="/AIHousing" onSelect={()=>this.reset()}>AI Housing</Nav.Link>: null}
          {pathname !== "/WHORE" ? <Nav.Link href="/WHORE" onSelect={()=>this.reset()}>W.H.O.R.E.-project</Nav.Link>: null}
        </Nav>
        <p className="text-monospace text-light">{pathname !== "/WHORE" ? "Credits:" : "Points:"} <span className="text-success font-weight-bold">{this.props.choices.credits}</span></p>
      </Navbar>
    )
  }
}

const mapStateToProps = (state) =>{
  return{
    meta: state.timeReducer,
    choices: state.choiceReducer
  }
}

const mapDispatchToProps = (dispatch) =>{
  return{
    reset: (payload) => {dispatch({type:"RESET_ALL"})},
    resetItems: (payload) => {dispatch({type:"RESET_ITEMS"})}
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(App);
