import Container from "react-bootstrap/Container"
import React from "react";
import Button from "react-bootstrap/Button";
const Text1 = (props) =>{
    

    return(
        <Container>
            <h6>Getting into the Witch High Orientation and Reorganization Educational-project was rather difficult. Especially for a witch as young as you. Only 18 and you had already succeeded in almost all training programs and the W.H.O.R.E-project was the only one left. Being the prodigy that you are, you were already fully aware that this was the hardest one yet. Only a few witches had succeeded in this program, but never the less you were determined to succeed no matter the cost.
                <br/>
                Not only are you one of the smartest witches of your time, but people seem to find you incredibly attractive. Many people have fought for your affection, but even though many had tempted you, your education has taken priority. 

            </h6>
            <Button block onClick={()=>{props.it()}}>Continue</Button>
        </Container>
    )
}

export default Text1;