import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import React from "react";
const Text1 = (props) =>{
        const item1 = {
            backdrops:null,
            torso: "default",
            legs:"default",
            arms_left: "default",
            arms_right: "default",
            pubic_hair:"growing",
            tits:"pressed",
            head:"default",
            genitalia: "default",
            squirt:"dripping",
            eyes: "brown_hypnotized",
            hair:props.character.hair,
            mouth:"nude_default",
            cheeks:null,
            nose:"default",
            emotions:null,
            outfit: {
                garterbelt:"french_maid_garter_belt",
                stockings: "lace_stocking",
                skirts: "low_micro",
                shirts: "tits.pressed/cheerleader_g_sexy",
                robes:"gryffindor_seethru_undressed",
            }};
        const item2 = {
            backdrops:"dungeon",
            torso: "default",
            legs:"default",
            arms_left: "default",
            arms_right: "default",
            pubic_hair:"growing",
            tits:"pressed",
            head:"default",
            genitalia: "default",
            squirt:props.character.squirt,
            eyes: "brown_glance_left",
            hair:props.character.hair,
            mouth:"nude_little_upset",
            cheeks:null,
            nose:"default",
            emotions:"sweat",
            outfit: {
                garterbelt:"french_maid_garter_belt",
                stockings: "lace_stocking",
                skirts: "low_micro",
                shirts: "tits.pressed/cheerleader_g_sexy",
                robes:"gryffindor_seethru_gap",
            }};
        return(
            <Container>
                <h6>As you walk out the horror hit you. You seem to be the only one with this kind of 'Uniform'. Even so, you are already late and so need to decide if you are going to skip class to talk with the Headmaster or do that after school.
                </h6>
                <Button onClick={()=>{
                    props.action({character:item1})
                    props.goControl()
                    props.it()}
                    } block>Go see Headmaster</Button>
                <Button onClick={()=>{
                    props.action({character:item2})
                    props.goTransformation()
                    props.it()}
                    } block>Go to class</Button>
            </Container>
        )    
}

export default Text1;