import React, {Component} from "react"
import Button from "react-bootstrap/Button"
import { connect } from 'react-redux';
import {getAlbum} from "../redux/firerockActions"
import Gamemode from "./gamemode";

class lewdFirerock extends Component {
    constructor(props) {
        super(props);
        this.state = { deck:null, images:[] }
    }

    componentDidMount(){
        
    }

    render() {
        console.log(this.props.images)
        
        switch(this.state.deck){
            case "Dominant":
                if(this.props.deckclass !== "Dominant"){
                    this.props.getalbum({url:"JQHLenJ", class:"Dominant"})
                }
                if(Number(this.props.images.length) > 0){
                    console.log(this.props.images.length, this.props.images)
                    return (<div className="col-md-10 offset-md-1">
                        <Gamemode deck={this.props.images} type="Dominant" description=""/>
                    </div>)
                }else{
                    return(<div className="col-md-10 offset-md-1">
                        <h1 className="text-center">Loading...</h1>
                    </div>)
                }
            case "Submissive":
                if(this.props.deckclass !== "Submissive"){
                    this.props.getalbum({url:"xgLqUGT", class:"Submissive"})
                }
                if(this.props.images.length > 0){
                    return (<div className="col-md-10 offset-md-1">
                        <Gamemode deck={this.props.images} type="Submissive" description=""/>
                    </div>)
                }else{
                    return(<div className="col-md-10 offset-md-1">
                        <h1 className="text-center">Loading...</h1>
                    </div>)
                }
                
            default:
                return ( <div>
                        <div className="col-md-10 offset-md-1 rounded dark-green-background">
                            <div>
                            <h3 className="text-center">Welcome to the Lewd Hearthstone helper</h3>
                            
                        </div>
                        <br/>
                        <div className="rounded light-green-background">
                            <h5>What is all this about?</h5>
                            <p>The idea is pretty simple. This page is designed to help people play a lewd version of hearthstone.</p>
                            <p>You start out by choosing a deck below. Then the website will generate you a random hand of cards from the deck.</p>
                        </div>
                        <div className="col-md-10 offset-md-1 rounded lime-background">
                        <Button block className="btn-lime" variant="outline-secondary" onClick={()=>this.setState({deck:"Dominant"})}>Give me a <strong>dominant</strong> deck</Button>
                        <Button block className="btn-lime" variant="outline-secondary" onClick={()=>this.setState({deck:"Submissive"})}>Give me a <strong>submissive</strong> deck</Button>
                        </div>
                    </div>
                </div> );
        }
    }
}
const MapStateToProps = (state) =>{
    console.log(state)
    return {
        images: state.firerockReducer.album,
        deckclass: state.firerockReducer.class
    }
}

const mapDispatchToProps = dispatch => {
    return {
      getalbum: payload => {
        dispatch(getAlbum(payload));
      }
    };
  };
  
  export default connect(
    MapStateToProps,
    mapDispatchToProps
  )(lewdFirerock); 